}
.contest-heading{
    font-size: 72px;
    background: -webkit-linear-gradient(#e50914, #333);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
    text-align: center;
}
.Rules-Btn{
