.upload-container{
    display: flex;
    justify-content: space-evenly;
}
div.Two.input {
    display: flex; /* Use flexbox */
    gap: 16px;
    flex-direction: row; /* Ensure items are arranged in a row */
}

div.Two.input .input-container {
    flex: 1; /* Allow each input to grow to take up available space */
    /* margin-right: 16px; */
}

/* Optional: Adjust input width */
div.Two.input .input-container input {
    width: 100%; /* Ensure input takes up all available space */
}

.SizeError{
    -webkit-animation: vibrate-2 0.3s linear infinite both;
    animation: vibrate-2 0.3s linear infinite both;
    font-weight: bold;
    color: #e50914;
    font-size: 20px;
}


@-webkit-keyframes vibrate-2 {
    0% {
      -webkit-transform: translate(0);
              transform: translate(0);
    }
    20% {
      -webkit-transform: translate(2px, -2px);
              transform: translate(2px, -2px);
    }
    40% {
      -webkit-transform: translate(2px, 2px);
              transform: translate(2px, 2px);
    }
    60% {
      -webkit-transform: translate(-2px, 2px);
              transform: translate(-2px, 2px);
    }
    80% {
      -webkit-transform: translate(-2px, -2px);
              transform: translate(-2px, -2px);
    }
    100% {
      -webkit-transform: translate(0);
              transform: translate(0);
    }
  }
  @keyframes vibrate-2 {
    0% {
      -webkit-transform: translate(0);
              transform: translate(0);
    }
    20% {
      -webkit-transform: translate(2px, -2px);
              transform: translate(2px, -2px);
    }
    40% {
      -webkit-transform: translate(2px, 2px);
              transform: translate(2px, 2px);
    }
    60% {
      -webkit-transform: translate(-2px, 2px);
              transform: translate(-2px, 2px);
    }
    80% {
      -webkit-transform: translate(-2px, -2px);
              transform: translate(-2px, -2px);
    }
    100% {
      -webkit-transform: translate(0);
              transform: translate(0);
    }
  }
  




@media screen and (max-width: 512px) {
    div.Two.input {
